@import "../../scss/breakpoints";

.page {
  $block-name: &; // #{$block-name}__element

  display: flex;
  flex-direction: column;
  min-height: calc(var(--vh) * 100);

  &__header {
    position: fixed;
    top: 0;
    z-index: 10;
    flex: 0 0 auto;
    width: 100%;

    // stylelint-disable
    .glightbox-open &,
    .hystmodal__opened & {
      width: calc(100% - var(--css-scroll-size));
    }
    // stylelint-enable
  }

  &__main {
    flex: 1 0 auto;
  }

  &__nav {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 9;
    width: 0;
  }

  &__nav-inner {
    width: 100vw;
    height: 100dvh;
    padding-top: 64px;
    background-color: var(--back-primary);
    transform: translateX(0);
    transition: transform 0.25s ease;

    @media screen and (min-width: $screen-tl) {
      display: none;
    }

    [data-state="mobile-menu"] & {
      transform: translateX(-100%);
    }
  }

  &__footer {
    flex: 0 0 auto;
  }
}
