@use "sass:color";

:root {
  --color-white: #ffffff;
  --color-black: #000000;

  --body-color: var(--front-primary);
  --body-bg: var(--back-primary);
  --font-base: var(--font-sans);
  --font-sans:
    Onest, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;

  --color-red: #dc2626;
  --color-grey-50: #fafafa;
  --color-grey-200: #e4e4e7;
  --color-grey-300: #d4d4d8;
  --color-grey-400: #a1a1aa;
  --color-grey-500: #71717a;
  --color-grey-600: #52525b;
  --color-grey-700: #3f3f46;
  --color-purple-200: #e9d5ff;
  --color-purple-300: #d8b4fe;
  --color-purple-400: #a065e5;
  --color-purple-500: #a855f7;
  --color-purple-600: #9333ea;
  --color-purple-900: #581c87;

  --front-primary: #18181b;
  --front-secondary: var(--color-grey-600);
  --front-invert: #ffffff;
  --back-primary: #ffffff;

  --border-color: #e4e4e7;
}
