@import "../../scss/breakpoints";
@import "../../scss/mixins/fluid";

.hifu {
  $block-name: &; // #{$block-name}__element

  position: relative;
  overflow: hidden;

  &__inner {
    @media screen and (min-width: $screen-ti) {
    	display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__main {
    position: relative;
    padding-top: fluid(24, 104, 360, 1360);
    padding-bottom: fluid(24, 104, 360, 1360);
    color: var(--front-invert);

    @media screen and (min-width: $screen-ti) {
      grid-column: 1/8;
      padding-right: 24px;
    }

    @media screen and (min-width: $screen-d) {
      position: static;
      padding: 104px 104px 104px 0;
    }

    &::before {
      position: absolute;
      inset: 0 -16px;
      z-index: -1;
      background: url("..//{{img}}//hifu__bg.webp") no-repeat top left, var(--color-purple-900);
      background-size: 235px auto, auto;
      border-radius: 0 48px 48px 0;
      content: "";

      @media screen and (min-width: $screen-ti) {
        inset: 0 0 0 -16px;
      }

      @media screen and (min-width: $screen-tl) {
        inset: 0 0 0 -40px;
      }

      @media screen and (min-width: $screen-d) {
        width: calc(100vw - (100vw - 80px) * 0.444);
        background-size: 408px auto, auto;
      }
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: fluid(24, 48, 360, 1360);
    line-height: 1.5;
    letter-spacing: -0.04em;

    @media screen and (min-width: $screen-dl) {
      font-size: 48px;
    }

    strong {
      color: var(--color-purple-300);
    }
  }

  &__subtitle {
    margin: 0;
    color: var(--color-purple-300);
    font-weight: 500;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }
  }

  &__p {
    margin: 24px 0 0;
    font-size: fluid(14, 24, 360, 1360);
    line-height: fluid(20, 32, 360, 1360);

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
      line-height: 1.5;
    }

    strong {
      color: var(--color-purple-300);
    }
  }

  &__miniature {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 48px 0;

    @media screen and (min-width: $screen-ti) {
      grid-column: 8/12;

      &::after {
        position: absolute;
        left: calc(100% + 24px);
        width: calc(100vw - 32px);
        height: 100%;
        background: var(--color-purple-900);
        border-radius: 48px 0 0 48px;
        content: "";
      }
    }
  }

  &__image {
    width: fluid(97, 136, 360, 1360);
    height: auto;

    @media screen and (min-width: $screen-dl) {
      width: 136px;
    }
  }
}
