@import "../../scss/breakpoints";

.form {
  $block-name: &; // #{$block-name}__element

  display: flex;
  flex-direction: column;
  gap: 16px;

  @media screen and (min-width: $screen-tl) {
    gap: 24px;
  }

  &__inner {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-bottom: auto;

    @media screen and (min-width: $screen-tl) {
      gap: 24px;
    }
  }
}
