@import "../../scss/breakpoints";
@import "../../scss/mixins/text-styles";

.specification {
  $block-name: &; // #{$block-name}__element

  padding: 88px 0;

  @media screen and (min-width: $screen-tl) {
  	padding: 104px 0;
  }

  &__title {
    @include title;

    margin: 0;

    strong {
      color: var(--color-purple-600);
      white-space: nowrap;
    }
  }

  &__cols {
    margin-top: 16px;

    @media screen and (min-width: $screen-tl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
      margin-top: 32px;
    }
  }

  &__aside {
    grid-column: 1/5;
    height: 100%;
  }

  &__image {
    @media screen and (min-width: $screen-tl) {
    	position: sticky;
      top: 120px;
    }
  }

  &__main {
    grid-column: 6/13;

    @media screen and (min-width: $screen-tl) {
      padding-top: 104px;
    }
  }

  &__grid {
    display: grid;
    gap: 16px;
    margin: 32px 0;

    @media screen and (min-width: $screen-tl) {
      gap: 24px;
      margin-top: 0;
    }

    @media screen and (min-width: $screen-d) {
    	grid-template-columns: auto auto;
    }
  }

  &__grid-inner {
    display: grid;
    gap: 16px;

    @media screen and (min-width: $screen-tl) {
      gap: 24px;
    }
  }

  &__label {
    color: var(--color-purple-600);
    font-weight: 500;
    font-size: fluid(12, 16, 360, 1360);
    line-height: fluid(16, 16, 360, 1360);
    letter-spacing: 0.03em;
    text-transform: uppercase;

    @media screen and (min-width: $screen-dl) {
      font-size: 16px;
      line-height: 1;
    }
  }

  &__grid-title {
    font-weight: 700;
    font-size: fluid(32, 40, 360, 1360);
    line-height: 1.25;

    @media screen and (min-width: $screen-dl) {
      font-size: 40px;
    }
  }

  &__p {
    margin: 0;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }
  }

  &__section {
    display: grid;
    gap: 8px;
    margin-top: 40px;

    @media screen and (min-width: $screen-tl) {
      margin-top: 64px;
    }
  }

  &__list {
    max-width: 536px;
  }

  &__sensors {
    position: relative;
  }

  &__sensor {
    margin-bottom: 8px;
  }

  &__button {
    display: inline-flex;
    gap: 12px;
    align-items: center;
    padding: 6px 22px 6px 14px;
    color: inherit;
    font-size: 16px;
    line-height: 1.25;
    text-decoration: none;
    background-color: rgba(255 255 255 / 85%);
    border: 2px solid var(--color-purple-200);
    border-radius: 32px;
    cursor: pointer;
    transition: background-color 0.25s ease;

    @media screen and (min-width: $screen-d) {
      position: absolute;
      top: 0;
      right: 16px;
      padding: 10px 22px 10px 14px;
    }

    &:hover {
      background-color: var(--color-purple-200);
    }
  }

  &__icon {
    width: 16px;
    height: 16px;
  }
}
