@import "../../scss/breakpoints";

.popup {
  $block-name: &; // #{$block-name}__element

  width: 100vw;
  min-height: 100dvh;
  overflow: hidden;

  @media screen and (min-width: $screen-t) {
  	max-width: 72%;
    min-height: auto;
    background-color: rgba(255 255 255 / 85%);
    border-radius: 48px;
    backdrop-filter: blur(12px);
  }

  @media screen and (min-width: $screen-tl) {
    max-width: 1024px;
    height: 640px;
  }

  &--thanks {
    width: 480px !important;
    height: 240px !important;
  }

  &__close {
    position: absolute;
    top: 12px;
    right: 12px;
    z-index: 10;
    display: block;
    width: 40px;
    height: 40px;
    padding: 8px;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    transition: background .25s ease;

    @media screen and (min-width: $screen-t) {
      top: 24px;
      right: 24px;
    }

    @media screen and (min-width: $screen-tl) {
      background-color: var(--color-white);
      border-radius: 50%;
    }

    &:focus,
    &:hover {
      outline: none;
    }
  }

  &__inner {
    height: 100%;

    @media screen and (min-width: $screen-tl) {
    	display: grid;
      grid-template-columns: repeat(12, 1fr);
    }
  }

  &__icon {
    width: 100%;
    height: 100%;
  }

  &__scroll {
    height: 100%;
    padding: 40px 16px 32px;

    @media screen and (min-width: $screen-t) {
      padding: 48px;
    }

    @media screen and (min-width: $screen-tl) {
      grid-column: 1/8;
      height: auto;
      overflow: hidden auto;
    }
  }

  &__form {
    height: 100%;

    @media screen and (min-width: $screen-tl) {
      height: auto;
    }
  }

  &__aside {
    display: none;

    @media screen and (min-width: $screen-tl) {
      display: block;
      grid-column: 8/13;
    }
  }

  &__image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__message {
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  &__title {
    margin: 0;
    color: var(--color-purple-600);
    font-size: 24px;
    line-height: 1.5;
    text-align: center;
  }

  &__p {
    margin: 0;
    font-size: 16px;
    line-height: 1.5;
    text-align: center;
  }
}
