@import "../../scss/breakpoints";
@import "../../scss/mixins/fluid";
@import "../../scss/mixins/list-reset";
@import "../../scss/mixins/text-styles";

.advantages {
  $block-name: &; // #{$block-name}__element

  padding: 88px 0 0;

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0;
  }

  &__cols {
    margin-bottom: 40px;

    @media screen and (min-width: $screen-tl) {
    	display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
      margin-bottom: 56px;
    }
  }

  &__main {
    @media screen and (min-width: $screen-tl) {
      grid-column: 1/8;
    }
  }

  &__title {
    @include title;

    margin: 0;

    strong {
      color: var(--color-purple-600);
    }
  }

  &__list {
    @include list-reset;

    margin-top: 24px;
    counter-reset: list2;

    @media screen and (min-width: $screen-tl) {
      margin-top: 40px;
    }
  }

  &__list-item {
    position: relative;
    padding: 8px 0 8px 24px;
    color: var(--color-grey-700);
    font-weight: 400;
    font-size: fluid(16, 20, 360, 1360);
    line-height: fluid(19, 24, 360, 1360);
    counter-increment: list2;

    @media screen and (min-width: $screen-tl) {
      padding: 8px 0 8px 32px;
    }

    @media screen and (min-width: $screen-dl) {
      font-size: 20px;
      line-height: 1.2;
    }

    & + & {
      box-shadow: inset 0 1px 0 var(--border-color);
    }

    &::before {
      position: absolute;
      top: 8px;
      left: 0;
      width: 24px;
      color: var(--color-purple-600);
      font-weight: 700;
      content: counter(list2) ".";
    }
  }

  &__aside {
    display: none;

    @media screen and (min-width: $screen-tl) {
      display: flex;
      grid-column: 8/13;
      align-items: center;
      justify-content: center;
    }
  }

  &__image {
    width: 72%;
    max-width: 368px;
    height: auto;
  }

  &__block {
    display: grid;
    gap: 16px;
    margin: 40px -16px 0;
    background: var(--color-grey-50);

    @media screen and (min-width: $screen-tl) {
      grid-template-columns: 1fr 1fr;
      gap: 0;
      margin: 0;
      padding: 0 24px;
      border-radius: 48px;
    }
  }

  &__item {
    position: relative;
    padding: 24px;

    @media screen and (min-width: $screen-tl) {
      padding: 40px 24px;
    }

    & + &::before {
      position: absolute;
      top: -8px;
      display: block;
      width: calc(100% - 48px);
      height: 1px;
      background-color: var(--color-grey-200);
      content: "";

      @media screen and (min-width: $screen-tl) {
        top: 40px;
        left: 0;
        width: 1px;
        height: calc(100% - 80px);
      }
    }
  }

  &__subtitle {
    margin: 0 0 fluid(12, 16, 360, 1360);
    font-weight: 500;
    font-size: fluid(20, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-dl) {
      margin: 0 0 16px;
      font-size: 24px;
    }

    strong {
      color: var(--color-purple-600);
      font-weight: 500;
    }
  }

  &__sublist {
    @include list-reset;

    display: grid;
    gap: fluid(8, 12, 360, 1360);

    @media screen and (min-width: $screen-dl) {
      gap: 12px;
    }
  }

  &__sublist-item {
    color: var(--color-grey-700);
    font-size: fluid(14, 16, 360, 1360);
    line-height: fluid(20, 20, 360, 1360);

    @media screen and (min-width: $screen-dl) {
      font-size: 16px;
      line-height: 1.25;
    }
  }
}
