@import "../../scss/mixins/text-styles";

.checkbox {
  $block-name: &; // #{$block-name}__element

  .just-validate-error-label {
    margin-top: 12px;
  }

  &__inner {
    position: relative;
    display: flex;
    gap: 8px;
    justify-self: start;
  }

  &__input {
    position: absolute;
    width: 16px;
    height: 16px;
    opacity: 0;
  }

  &__fake-input {
    width: 16px;
    height: 16px;
    background: var(--back-primary);
    border: 1px solid var(--color-grey-300);
    border-radius: 4px;
    cursor: pointer;
    transition: background 0.125s ease;

    &::after {
      display: block;
      width: 10px;
      height: 10px;
      margin: 2px;
      border-radius: 2px;
      transition: background-color 0.125s ease;
      content: "";
    }
  }

  &__input:checked + &__fake-input {
    background: var(--brand-primary);
  }

  &__input[disabled]:checked + &__fake-input {
    background: var(--brand-primary-light);
    border: 1px solid var(--color-purple-600);
    cursor: not-allowed;
  }

  &__input:checked + &__fake-input::after {
    background-color: var(--color-purple-600);
  }

  &__input[disabled]:checked + &__fake-input::after {
    border-color: var(--front-invert);
    opacity: 0.5;
  }

  &__label {
    @include checkbox-label;

    cursor: pointer;
    user-select: none;
  }

  &__input[disabled] ~ &__label {
    color: var(--front-tertiary);
    cursor: not-allowed;
  }
}
