@import "../../scss/breakpoints";
@import "../../scss/mixins/button-reset";
@import "../../scss/mixins/flexbox";
@import "../../scss/mixins/link-reset";

.header {
  $block-name: &; // #{$block-name}__element

  background-color: rgba(255 255 255 / 85%);
  backdrop-filter: blur(12px);

  &__inner {
    @include flex-center-sb;

    padding: 16px 0;

    @media screen and (min-width: $screen-tl) {
      padding: 32px 0;
    }
  }

  &__logo {
    @include button-reset();
  }

  &__nav {
    display: none;
    gap: 32px;

    @media screen and (min-width: $screen-tl) {
      display: flex;
    }
  }

  &__link {
    @include link-reset;

    font-weight: 500;
    font-size: 14px;
    line-height: 1.1429;
    transition: color 0.25s ease-out;

    &:hover {
      color: var(--color-purple-500);
    }
  }

  &__burger {
    @include button-reset();

    position: relative;
    display: flex;
    flex-direction: column;
    gap: 4px;
    align-items: center;
    justify-content: center;
    width: 32px;
    height: 32px;
    padding: 0 7px;

    @media screen and (min-width: $screen-tl) {
      display: none;
    }
  }

  &__burger-line {
    position: relative;
    transition: color 0.25s ease-out;

    &,
    &::before,
    &::after {
      width: 18px;
      height: 1.5px;
      background-color: var(--color-black);
      transform-origin: center;
    }

    &::before,
    &::after {
      position: absolute;
      display: block;
      transition: top 0.25s ease-out 0.25s, bottom 0.25s ease-out 0.25s, transform 0.25s ease-out;
      content: "";
    }

    &::before {
      top: -5px;
    }

    &::after {
      bottom: -5px;
    }

    [data-state="mobile-menu"] & {
      background-color: transparent;

      &::before,
      &::after {
        transition: top 0.25s ease-out, bottom 0.25s ease-out, transform 0.25s ease-out 0.25s;
      }

      &::before {
        top: 0;
        transform: rotate(45deg);
      }

      &::after {
        bottom: 0;
        transform: rotate(-45deg);
      }
    }
  }
}
