@import "../../scss/breakpoints";
@import "../../scss/mixins/fluid";
@import "../../scss/mixins/text-styles";

.producer {
  $block-name: &; // #{$block-name}__element

  padding-top: 88px;

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0;
  }

  &__cols {
    @media screen and (min-width: $screen-d) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
    }
  }

  &__main {
    @media screen and (min-width: $screen-d) {
      grid-column: 1/8;
    }
  }

  &__title {
    @include title;

    margin: 0;
  }

  &__logo {
    margin: 32px 0 24px;
  }

  &__lead {
    margin: 0;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-tl) {
      font-size: 24px;
    }
  }

  &__p {
    margin: 24px 0 0;
    font-size: fluid(14, 20, 360, 1360);
    line-height: fluid(20, 30, 360, 1360);

    @media screen and (min-width: $screen-tl) {
      margin-top: 32px;
      font-size: 20px;
      line-height: 1.5;
    }
  }

  &__aside {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 32px;

    @media screen and (min-width: $screen-d) {
      grid-column: 8/13;
    }
  }
}
