@import "../../scss/breakpoints";
@import "../../scss/mixins/text-styles";
@import "swiper/swiper";

.before-after {
  $block-name: &; // #{$block-name}__element

  padding: 88px 0 48px;
  overflow: hidden;

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0;
  }

  &__inner {
    @media screen and (min-width: $screen-t) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__title {
    @include title;

    margin: 0;

    @media screen and (min-width: $screen-t) {
      text-align: center;
    }

    strong {
      color: var(--color-purple-600);
      white-space: nowrap;
    }
  }

  &__carousel {
    width: 100%;
    margin: 32px 0 40px;
    overflow: visible;

    @media screen and (min-width: $screen-tl) {
      margin: 56px 0 72px;
    }
  }

  &__slide {
    overflow: hidden;
    border-radius: 24px;
  }

  &__image {
    width: 100%;
    max-width: none;
    height: auto;
  }

  &__button {
    display: inline-block;
    width: 100%;
    max-width: 424px;
    margin: 0 auto;
  }
}
