@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin flex-center-sb {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@mixin flex-center-ac {
  display: flex;
  align-items: center;
}

@mixin flex-column {
  display: flex;
  flex-direction: column;
}

@mixin flex-column-jc {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@mixin flex-column-cn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
