@import "../breakpoints";
@import "fluid";

@mixin body {
  font-weight: 400;
  font-size: 16px;
  line-height: 1;
}

@mixin title {
  font-weight: 700;
  font-size: fluid(24, 48, 360, 1360);
  line-height: 1.25;
  letter-spacing: -0.06rem;

  @media screen and (min-width: $screen-dl) {
    font-size: 48px;
  }
}

@mixin input {
  color: var(--colors-zinc-900, #18181B);
  font-size: 16px;
  font-family: inherit;
  line-height: 1;
}

@mixin textarea {
  font-size: 16px;
  line-height: 1.25;
}

@mixin field {
  font-weight: 500;
  font-size: fluid(12, 14, 360, 1360);
  line-height: fluid(16, 16, 360, 1360);

  @media screen and (min-width: $screen-dl) {
    font-size: 16px;
    line-height: 1.1429;
  }
}

@mixin checkbox-label {
  font-size: 14px;
  line-height: 1.1429;
}
