@import "../../scss/breakpoints";
@import "../../scss/mixins/list-reset";
@import "../../scss/mixins/bg-image";
@import "../../scss/mixins/flexbox";
@import "../../scss/mixins/fluid";

.hero {
  $block-name: &; // #{$block-name}__element

  @include bg-image("contain", "0 64px");

  height: 100vh;
  min-height: 560px;
  padding: 64px 0 32px;
  background-image: url("..//{{img}}//hero__bg.webp");

  @media screen and (min-width: $screen-tl) {
    padding: 96px 0 0;
    background-position: 0 0;
  }

  .container {
    height: 100%;

    @media screen and (min-width: $screen-dl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__inner {
    display: flex;
    align-items: center;
    height: 100%;

    @media screen and (min-width: $screen-ti) {
      display: grid;
      grid-template-columns: repeat(10, 1fr);
      gap: 24px;
      align-content: center;
    }

    @media screen and (min-width: $screen-dl) {
      grid-column: 2 / 12;
    }
  }

  &__info {
    @media screen and (min-width: $screen-ti) {
      grid-column: 1/8;
    }
  }

  &__image {
    display: none;
    grid-column: 8/11;
    justify-content: center;

    @media screen and (min-width: $screen-ti) {
      display: flex;
    }
  }

  &__title {
    margin: 0;
    color: var(--color-purple-600);
    font-weight: 700;
    font-size: fluid(28, 64, 360, 1360);
    line-height: 1.25;
    letter-spacing: -0.04em;

    @media screen and (min-width: $screen-dl) {
      font-size: 64px;
    }
  }

  &__p {
    margin: 24px 0 0;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.3334;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }
  }

  &__list {
    @include flex-column;
    @include list-reset;

    gap: 12px;
    margin-top: 32px;
  }

  &__list-item {
    position: relative;
    padding-left: 20px;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.2;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }

    strong {
      color: var(--color-purple-600);
      font-weight: 400;
    }

    &::before {
      position: absolute;
      top: 50%;
      left: 0;
      width: 8px;
      height: 8px;
      background-color: var(--color-purple-600);
      transform: translateY(-50%);
      content: "";
    }
  }

  &__button {
    width: 100%;
    margin-top: 48px;

    @media screen and (min-width: $screen-t) {
      width: 312px;
    }

    @media screen and (min-width: $screen-tl) {
      margin-top: 56px;
    }
  }
}
