@import "../../scss/breakpoints";

.method {
  $block-name: &; // #{$block-name}__element

  padding: 88px 0 48px;

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0;
  }

  &__inner {
    @media screen and (min-width: $screen-tl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__miniature {
    display: none;
    grid-column: 1/5;
    align-items: start;
    justify-content: center;

    @media screen and (min-width: $screen-tl) {
      display: flex;
    }
  }

  &__image {
    display: block;
    width: 80%;
    max-width: 312px;
    height: auto;
    border-radius: 50%;
  }

  &__info {
    @media screen and (min-width: $screen-tl) {
      grid-column: 5/13;
      padding-top: 24px;
    }
  }

  &__lead {
    margin: 0;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }

    strong {
      color: var(--color-purple-600);
    }
  }

  &__p {
    margin: 24px 0 0;
    color: var(--color-grey-600);
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }
  }

  &__button {
    width: 100%;
    margin-top: 40px;

    @media screen and (min-width: $screen-t) {
      width: 423px;
    }
  }
}
