@import "../../scss/breakpoints";
@import "../../scss/mixins/text-styles";

.feedback {
  $block-name: &; // #{$block-name}__element

  padding: 88px 0 48px;
  overflow: hidden;
  background: linear-gradient(180deg, #faf5ff 0%, rgba(250 245 255 / 0%) 100%);

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0;
  }

  .container {
    @media screen and (min-width: $screen-tl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__inner {
    @media screen and (min-width: $screen-tl) {
      grid-column: 2/12;
    }
  }

  &__title {
    @include title;

    margin: 0;

    strong {
      color: var(--color-purple-600);
      white-space: nowrap;
    }
  }

  &__p {
    margin: 24px 0 40px;
    color: var(--color-grey-500);
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }
  }

  &__cols {
    @media screen and (min-width: $screen-tl) {
      display: grid;
      grid-template-columns: repeat(10, 1fr);
      gap: 24px;
    }
  }

  &__main {
    @media screen and (min-width: $screen-tl) {
      grid-column: 1/5;
    }
  }

  &__aside {
    display: none;

    @media screen and (min-width: $screen-tl) {
      display: flex;
      grid-column: 6/11;
      align-items: start;
      justify-content: center;
    }
  }

  &__aside-inner {
    position: relative;
  }

  &__form {
    max-width: 424px;

    .textarea {
      @media screen and (min-width: $screen-tl) {
        min-height: 160px;
      }
    }
  }

  &__checkbox {
    margin: 24px 0 8px;
  }

  &__pattern {
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: -1;
    display: none;
    width: 125%;
    max-width: none;
    height: auto;
    transform: translate(-55%, 15%);

    @media screen and (min-width: $screen-tl) {
      display: block;
    }
  }
}
