@import "../../scss/mixins/text-styles";

.field {
  $block-name: &; // #{$block-name}__element

  &__inner {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__label {
    @include field;

    display: flex;
    align-items: baseline;
    justify-content: space-between;
    order: 1;

    #{$block-name}__input:not([required]) + &::after {
      margin-left: 2px;
      color: var(--color-grey-400);
      font-weight: 500;
      font-size: 12px;
      line-height: 1.1429;
      content: "не обязательно";
    }
  }

  &__input,
  &__select,
  &__textarea {
    order: 2;
  }
}

.just-validate-error-label,
.error-message {
  @include field;

  order: 3;
  color: var(--color-red);
}
