@import "../../scss/breakpoints";
@import "../../scss/mixins/button-reset";
@import "../../scss/mixins/fluid";
@import "../../scss/mixins/list-reset";
@import "../../scss/mixins/text-styles";
@import "glightbox/dist/css/glightbox.css";

.technology {
  $block-name: &; // #{$block-name}__element

  padding: 88px 0 48px;
  overflow: hidden;
  background: url("..//{{img}}//technology__bg.webp") no-repeat center bottom;
  background-size: cover;

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0 160px;
  }

  &__title {
    @include title;

    margin: 0;

    strong {
      color: var(--color-purple-600);
    }
  }

  &__cols {
    margin: 24px 0 0;

    @media screen and (min-width: $screen-tl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
      margin: 32px 0 0;
    }
  }

  &__main {
    @media screen and (min-width: $screen-tl) {
      grid-column: 1/9;
    }
  }

  &__p {
    margin: 0;
    font-size: fluid(14, 24, 360, 1360);
    line-height: 1.5;
  }

  &__lead {
    margin: 24px 0 0;
    font-size: fluid(20, 32, 360, 1360);
    line-height: 1.2;

    @media screen and (min-width: $screen-tl) {
      margin: 40px 0 0;
    }

    @media screen and (min-width: $screen-dl) {
      font-size: 32px;
    }

    strong {
      color: var(--color-purple-600);
    }
  }

  &__aside {
    margin-top: 40px;

    @media screen and (min-width: $screen-tl) {
      grid-column: 10/13;
      margin: 0;
    }
  }

  &__subtitle {
    margin: 0 0 16px;
    color: var(--color-purple-600);
    font-weight: 700;
    font-size: fluid(20, 24, 360, 1360);
    line-height: fluid(24, 36, 360, 1360);

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
      line-height: 1.5;
    }
  }

  &__list {
    @include list-reset;

    display: grid;
    gap: 16px;
    counter-reset: list;
  }

  &__list-item {
    position: relative;
    padding: 8px 0 8px 44px;
    font-size: fluid(16, 20, 360, 1360);
    line-height: fluid(16, 24, 360, 1360);
    white-space: nowrap;
    counter-increment: list;

    @media screen and (min-width: $screen-tl) {
    	white-space: normal;
    }

    @media screen and (min-width: $screen-dl) {
      font-size: 20px;
      line-height: 1.2;
    }

    & + &::after {
      position: absolute;
      top: -24px;
      left: 15px;
      display: block;
      width: 2px;
      height: 32px;
      background-color: var(--color-purple-600);
      content: "";

      @media screen and (min-width: $screen-tl) {
        top: -48px;
        height: 48px;
      }
    }

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 32px;
      height: 32px;
      font-weight: 700;
      font-size: 20px;
      line-height: 1.3;
      background-color: var(--color-white);
      border: 2px solid var(--color-purple-600);
      border-radius: 50%;
      content: counter(list);
    }
  }

  &__video {
    @include button-reset;

    position: relative;
    margin: 48px -16px;

    @media screen and (min-width: $screen-tl) {
      margin: 104px -40px;
    }

    @media screen and (min-width: $screen-d) {
      margin: 104px 0;
      overflow: hidden;
      border-radius: 48px;
    }
  }

  &__video-bg {
    width: 100%;
    max-width: none;
    height: auto;
  }

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: auto;
    transform: translate(-50%, -50%);

    @media screen and (min-width: $screen-d) {
      width: 104px;
    }
  }

  &__block {
    @media screen and (min-width: $screen-tl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
      align-items: center;
    }
  }

  &__miniature {
    position: relative;

    @media screen and (min-width: $screen-tl) {
      display: flex;
      grid-row: 1;
      grid-column: 8/12;
      justify-content: center;
    }
  }

  &__pattern {
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: -1;
    display: none;
    width: 125%;
    max-width: none;
    height: auto;
    transform: translate(-55%, 30%);

    @media screen and (min-width: $screen-tl) {
      display: block;
    }
  }

  &__info {
    margin-top: 32px;

    @media screen and (min-width: $screen-tl) {
      grid-row: 1;
      grid-column: 1/8;
    }
  }

  &__addition {
    margin: 0;
    font-size: fluid(16, 24, 360, 1360);
    line-height: fluid(24, 32, 360, 1360);

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
      line-height: 1.3334;
    }

    strong {
      color: var(--color-purple-600);
    }
  }

  &__row {
    display: grid;
    gap: 12px;
    margin-top: 32px;

    @media screen and (min-width: $screen-t) {
      grid-template-columns: auto auto;
      gap: 24px;
      align-items: center;
    }

    @media screen and (min-width: $screen-tl) {
      grid-template-columns: auto;
      margin-top: 40px;
    }

    @media screen and (min-width: $screen-t) {
      gap: 12px;
    }

    @media screen and (min-width: $screen-d) {
      grid-template-columns: auto auto;
      gap: 24px;
      align-items: center;
    }
  }

  &__button {
    width: 100%;

    @media screen and (min-width: $screen-t) {
      width: auto;
    }
  }

  &__small {
    color: var(--color-grey-600);
    font-size: 12px;
    line-height: 1.5;
    text-align: center;

    @media screen and (min-width: $screen-t) {
      max-width: 236px;
      text-align: left;
    }

    @media screen and (min-width: $screen-tl) {
      max-width: none;
      text-align: center;
    }

    @media screen and (min-width: $screen-d) {
      max-width: 236px;
      text-align: left;
    }
  }
}
