@import "../../scss/breakpoints";
@import "../../scss/mixins/link-reset";

.nav {
  $block-name: &; // #{$block-name}__element

  height: 100%;

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 15vh 0 24px;
    overflow: hidden auto;
  }

  &__list {
    display: grid;
    gap: 40px;
    justify-items: center;
  }

  &__link {
    @include link-reset;

    color: var(--color-purple-600);
    font-size: 20px;
    line-height: 1;
    text-align: center;
    transition: color 0.25s ease;

    &:hover {
      color: var(--front-primary);
    }
  }

  &__socials {
    display: grid;
    gap: 12px;
    justify-items: center;
    margin-top: 48px;
  }

  &__social {
    @include link-reset;

    display: flex;
    gap: 12px;
    align-items: center;
    font-size: 16px;
    line-height: 1.25;

    &:hover {
      opacity: 0.75;
      transition: opacity 0.25s ease;
    }
  }

  &__icon {
    width: 32px;
    height: 32px;
  }
}
