@import "../../scss/breakpoints";
@import "../../scss/mixins/link-reset";

.footer {
  $block-name: &; // #{$block-name}__element

  color: var(--front-invert);
  background-color: var(--color-purple-400);
  scroll-margin-top: 64px;

  .container {
    @media screen and (min-width: $screen-dl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__inner {
    display: grid;
    gap: 16px;
    padding: 48px 0;

    @media screen and (min-width: $screen-t) {
    	grid-template-columns: 152px auto;
      gap: 24px;
    }

    @media screen and (min-width: $screen-tl) {
      grid-template-columns: 200px auto;
      padding: 56px 0 72px;
    }

    @media screen and (min-width: $screen-dl) {
      grid-column: 2 / 13;
    }
  }

  &__logo {
    width: 152px;
    height: 152px;

    @media screen and (min-width: $screen-tl) {
      width: 200px;
      height: 200px;
    }
  }

  &__info {
    display: grid;
    gap: 32px;

    @media screen and (min-width: $screen-tl) {
      gap: 20px;
      padding-top: 24px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 15px;
    line-height: 1.3334;

    @media screen and (min-width: $screen-tl) {
      font-size: 24px;
      line-height: 1.25;
    }
  }

  &__grid {
    display: grid;
    gap: 24px;

    @media screen and (min-width: $screen-d) {
      grid-template-columns: auto 1px auto;
    }
  }

  &__block {
    display: grid;
    gap: 16px;
    justify-content: start;

    @media screen and (min-width: $screen-d) {
      gap: 8px;
    }
  }

  &__divider {
    width: 100%;
    height: 1px;
    background-color: var(--front-invert);
    opacity: 0.5;

    @media screen and (min-width: $screen-d) {
      width: 1px;
      height: 100%;
    }
  }

  &__link {
    @include link-reset;

    display: flex;
    gap: 12px;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.25;
    transition: opacity 0.25s ease;

    @media screen and (min-width: $screen-tl) {
      font-size: 20px;
    }

    &:hover {
      opacity: 0.75;
    }
  }

  &__icon {
    flex: 0 0 auto;
    width: 32px;
    height: 32px;
  }

  &__socials {
    display: grid;
    gap: 12px;
    justify-content: start;
    margin-top: 16px;

    @media screen and (min-width: $screen-tl) {
      margin-top: 28px;
    }
  }
}
