@import "../../scss/breakpoints";
@import "../../scss/mixins/fluid";
@import "../../scss/mixins/link-reset";

.contacts {
  $block-name: &; // #{$block-name}__element

  padding: 16px 0 0;

  @media screen and (min-width: $screen-ti) {
    padding-bottom: 64px;
  }

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0;
  }

  .container {
    @media screen and (min-width: $screen-tl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__inner {
    @media screen and (min-width: $screen-tl) {
      grid-column: 2/12;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: fluid(20, 32, 360, 1360);
    line-height: fluid(24, 40, 360, 1360);

    @media screen and (min-width: $screen-tl) {
      text-align: center;
    }

    @media screen and (min-width: $screen-dl) {
      font-size: 32px;
      line-height: 1.25;
    }

    strong {
      color: var(--color-purple-600);
      white-space: nowrap;
    }
  }

  &__block {
    display: grid;
    gap: 16px;
    margin: 16px -16px 0;
    padding-bottom: 32px;
    background: var(--color-grey-50);

    @media screen and (min-width: $screen-ti) {
      grid-template-columns: 1fr 1fr;
      gap: 0;
      margin: 40px 0 0;
      padding: 0;
      border-radius: 48px;
    }
  }

  &__item {
    position: relative;
    display: grid;
    gap: 16px;
    justify-items: start;
    padding: 16px;

    @media screen and (min-width: $screen-ti) {
      padding: 32px 48px;
    }

    & + &::before {
      position: absolute;
      top: -8px;
      display: block;
      width: calc(100% - 32px);
      height: 1px;
      background-color: var(--color-grey-200);
      content: "";

      @media screen and (min-width: $screen-ti) {
        top: 48px;
        left: 0;
        width: 1px;
        height: calc(100% - 96px);
      }
    }
  }

  &__subtitle {
    margin: 0;
    color: var(--color-purple-600);
    font-weight: 700;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;
    white-space: nowrap;

    @media screen and (min-width: $screen-dl) {
      font-size: 24px;
    }
  }

  &__row {
    display: flex;
    gap: 8px;
  }

  &__row-inner {
    display: grid;
    gap: 4px;
    justify-items: start;
  }

  &__row-title {
    font-size: fluid(16, 20, 360, 1360);
    line-height: 1.2;

    @media screen and (min-width: $screen-dl) {
      font-size: 20px;
    }
  }

  &__link {
    display: flex;
    gap: 8px;
    align-items: center;
    color: var(--color-purple-600);
    font-weight: 700;
    font-size: 16px;
    line-height: 1.2;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &__sublink {
    color: var(--color-purple-600);
    font-weight: 500;
    font-size: 16px;
    line-height: 1.2;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  &__icon {
    width: 32px;
    height: 32px;
    color: var(--color-grey-500);
  }
}
