@import "../../scss/breakpoints";
@import "../../scss/mixins/fluid";

.about {
  $block-name: &; // #{$block-name}__element

  padding: 88px 0 0;

  @media screen and (min-width: $screen-tl) {
    padding: 104px 0 0;
  }

  &__row {
    @media screen and (min-width: $screen-dl) {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 24px;
    }
  }

  &__p {
    margin: 0;
    font-size: fluid(16, 24, 360, 1360);
    line-height: 1.5;

    @media screen and (min-width: $screen-dl) {
      grid-column: 2/11;
      font-size: 24px;
    }

    strong {
      color: var(--color-purple-600);
    }
  }

  &__scroll {
    height: 190px;
    margin: 24px -16px;
    padding: 0 16px;
    overflow: auto hidden;

    @media screen and (min-width: $screen-t) {
      height: auto;
      margin: 24px 0;
    }

    @media screen and (min-width: $screen-tl) {
      margin: 64px 0;
    }
  }

  &__schema {
    display: block;
    width: auto;
    max-width: none;
    height: 100%;

    @media screen and (min-width: $screen-t) {
      width: 100%;
      height: auto;
    }
  }

  &__block {
    margin: 24px -16px 0;
    padding: 16px 16px 48px;
    background: linear-gradient(180deg, #faf5ff 0, #fafafa 100%);
    border-radius: 88px 88px 0 0;

    @media screen and (min-width: $screen-ti) {
    	display: grid;
      grid-template-columns: repeat(24, 1fr);
      margin: 48px -16px;
      padding: 16px;
      border-radius: 176px;
    }

    @media screen and (min-width: $screen-tl) {
      margin: 104px 0;
    }
  }

  &__image {
    width: calc(100% + 16px);
    max-width: none;
    height: auto;
    margin: -8px;
    aspect-ratio: 344/224;
    object-fit: cover;
    object-position: center;
    border-radius: 88px;

    @media screen and (min-width: $screen-ti) {
      grid-column: 1/11;
      width: 100%;
      height: 100%;
      margin: 0;
      aspect-ratio: unset;
      border-radius: 156px;
    }
  }

  &__list {
    padding-top: 32px;

    @media screen and (min-width: $screen-ti) {
      grid-column: 12/23;
    }
  }

  &__subtitle {
    margin-bottom: 8px;
    color: var(--color-purple-500);
    font-weight: 500;
    font-size: 16px;
    line-height: 1;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    @media screen and (min-width: $screen-ti) {
      margin-bottom: 16px;
    }
  }
}
